<script>
  /**
   * 
   * @module widgets/xdh-menu
   * @example
   *
   * // 
   */
  export default {
    name: 'XdhMenu',
    /**
     * 
     * @property {Array} [data = []] data 
     * @property {Object} [props = { id: 'id', text: 'text', icon: 'icon', children: 'children', group: 'group', route: 'route' }] props data {g_id:1, str:''}, "props={id: 'g_id', text: 'str'}"g_ididstrtext
     * @property {String} [mode = 'verticle'] mode  horizontal
     * @property {Boolean} [collapse = false]  mode  vertical 
     * @property {String} [backgroundColor] backgroundColor='#FFFFFF'  hex 
     * @property {String} [textColor]  hex 
     * @property {String} [activeTextColor]  hex 
     * @property {String} [defaultActive]  index defaultActive = '1'
     * @property {Array} [defaultOpeneds] sub-menu key  defaultOpeneds = ['2', '4']
     * @property {Boolean} [uniqueOpened = false] 
     * @property {String} [menuTrigger = 'click'] ( mode  horizontal )
     * @property {Boolean} [router = false]  vue-router  index  path 
     */
    props: {
      // 
      data: {
        type: Array,
        default() {
          return []
        }
      },
      // 
      props: {
        type: Object,
        default() {
          return {
            id: 'id',
            text: 'text',
            icon: 'icon',
            children: 'children',
            group: 'group',
            route: 'route'
          }
        }
      },
      //  horizontal / vertical
      mode: String,

      //  mode  vertical 
      collapse: Boolean,

      //  hex 
      backgroundColor: String,

      //  hex 
      textColor: String,

      //  hex 
      activeTextColor: String,

      //  index
      defaultActive: String,

      // sub-menu key 
      defaultOpeneds: Array,

      // 
      uniqueOpened: Boolean,

      // ( mode  horizontal )
      menuTrigger: String,

      //  vue-router  index  path 
      router: Boolean,

      // 
      popperClass: String
    },
    methods: {
      grouping(array) {
        let groups = {
          'default': []
        }
        let props = this.props
        array.forEach(n => {
          let key = n[props.group]
          if (key) {
            groups[key] = groups[key] || []
            groups[key].push(n)
          } else {
            groups['default'].push(n)
          }
        })
        return groups
      },
      createTitle(h, item) {
        return [
          h('i', {
            'class': item[this.props.icon]
          }),
          h('span', {
            slot: 'title'
          }, item[this.props.text])
        ]
      },
      createItem(h, item) {
        const key = (item[this.props.id] || '').toString()
        return h('el-menu-item', {
          props: {
            index: key,
            route: item[this.props.route]
          },
          key: key
        }, this.createTitle(h, item))
      },
      createSubmenu(h, item) {
        const key = (item[this.props.id] || '').toString()
        return h('el-submenu', {
          props: {
            index: (item[this.props.id] || '').toString(),
            popperClass: this.popperClass
          },
          key: key
        }, [h('template', {
          slot: 'title'
        }, this.createTitle(h, item)), this.createNodes(h, item[this.props.children])])
      },
      createGroup(h, title, items) {
        let nodes = []
        let props = this.props
        items.forEach(item => {
          if (item[props.children] && item[props.children].length > 0) {
            nodes.push(this.createSubmenu(h, item))
          } else {
            nodes.push(this.createItem(h, item))
          }
        })
        nodes.unshift(h('template', {
          slot: 'title'
        }, title))
        return h('el-menu-item-group', null, nodes)
      },
      createNodes(h, array) {
        let nodes = [], groups = this.grouping(array)
        let props = this.props
        for (let key in groups) {
          let items = groups[key] || []
          if (key === 'default') {
            items.forEach(item => {
              if (item[props.children] && item[props.children].length > 0) {
                nodes.push(this.createSubmenu(h, item))
              } else {
                nodes.push(this.createItem(h, item))
              }
            })
          } else {
            nodes.push(this.createGroup(h, key, items))
          }
        }
        return nodes
      },
      open(index) {
        this.$refs.menu.open(index)
      },
      close(index) {
        this.$refs.menu.close(index)
      }
    },
    render(h) {
      console.log(this.$props, 'this.$props')
      return h(
        'el-menu',
        {
          props: {
            ...this.$props
          },
          'class': 'xdh-menu',
          on: {
            /**
             * 
             * @event select
             * @param index {String} id
             * @param indexPath {Array}  ['item1', 'item1-1']
             */
            select: (index, indexPath) => this.$emit('select', index, indexPath),
            /**
             * 
             * @event open
             * @param index {String} id
             * @param indexPath {Array}  ['item1', 'item1-1']
             */
            open: (index, indexPath) => this.$emit('open', index, indexPath),
            /**
             * 
             * @event close
             * @param index {String} id
             * @param indexPath {Array}  ['item1', 'item1-1']
             */
            close: (index, indexPath) => this.$emit('close', index, indexPath)
          },
          ref: 'menu'
        },
        this.createNodes(h, this.data))
    }
  }
</script>
