<template>
  <div class="xdh-menu-toggle" :class="{'is-collapse': collapse}">
    <!--el-scrollbar100%dom-->
    <el-scrollbar class="xdh-menu-toggle--expand"> <!--width/height:100%-->
      <xdh-menu :data="data"
                :props="props"
                :collapse="false"
                :backgroundColor="backgroundColor"
                :textColor="textColor"
                :activeTextColor="activeTextColor"
                :defaultActive="currentIndex"
                :defaultOpeneds="defaultOpeneds"
                :uniqueOpened="uniqueOpened"
                :menuTrigger="menuTrigger"
                :router="router"
                :popperClass="popperClass"
                @open="handleOpen"
                @close="handleClose"
                @select="handleSelect"></xdh-menu>
    </el-scrollbar>

    <xdh-menu :data="data"
              class="xdh-menu-toggle--collapse"
              :props="props"
              :collapse="true"
              :backgroundColor="backgroundColor"
              :textColor="textColor"
              :activeTextColor="activeTextColor"
              :defaultActive="currentIndex"
              :defaultOpeneds="defaultOpeneds"
              :uniqueOpened="uniqueOpened"
              :menuTrigger="menuTrigger"
              :router="router"
              :popperClass="popperClass"
              @open="handleOpen"
              @close="handleClose"
              @select="handleSelect"></xdh-menu>

  </div>
</template>

<script>
  /**
   * 
   * @module widgets/xdh-menu-toggle
   * @example
   *
   * // 
   */
  import XdhMenu from '../xdh-menu/xdh-menu.vue'

  export default {
    name: 'XdhMenuToggle',
    components: {
      XdhMenu
    },
    /**
     * 
     * @property {Array} [data = []] data 
     * @property {Object} [props = { id: 'id', text: 'text', icon: 'icon', children: 'children', group: 'group', route: 'route' }] props data {g_id:1, str:''}, "props={id: 'g_id', text: 'str'}"g_ididstrtext
     * @property {String} [mode = 'verticle'] mode  horizontal
     * @property {Boolean} [collapse = false]  mode  vertical 
     * @property {String} [backgroundColor] backgroundColor='#FFFFFF'  hex 
     * @property {String} [textColor]  hex 
     * @property {String} [activeTextColor]  hex 
     * @property {String} [defaultActive]  index defaultActive = '1'
     * @property {Array} [defaultOpeneds] sub-menu key  defaultOpeneds = ['2', '4']
     * @property {Boolean} [uniqueOpened = false] 
     * @property {String} [menuTrigger = 'click'] ( mode  horizontal )
     * @property {Boolean} [router = false]  vue-router  index  path 
     */
    props: {
      // 
      data: {
        type: Array,
        default () {
          return []
        }
      },
      // 
      props: {
        type: Object,
        default () {
          return {
            id: 'id',
            text: 'text',
            icon: 'icon',
            children: 'children',
            group: 'group'
          }
        }
      },
      //  mode  vertical 
      collapse: Boolean,

      //  hex 
      backgroundColor: String,

      //  hex 
      textColor: String,

      //  hex 
      activeTextColor: String,

      //  index
      defaultActive: String,

      // sub-menu key 
      defaultOpeneds: Array,

      // 
      uniqueOpened: Boolean,

      // ( mode  horizontal )
      menuTrigger: String,

      //  vue-router  index  path 
      router: Boolean,

      // 
      popperClass: String
    },
    data () {
      return {
        currentIndex: this.defaultActive
      }
    },
    watch: {
      /**
       * toggle 
       * @event change
       * @param val {boolean} collapse
       */
      collapse (val) {
        this.$emit('change', val)
      },
      defaultActive (val) {
        this.currentIndex = val
      }
    },
    methods: {
      /**
       * 
       * @event select
       * @param index {String} id
       * @param indexPath {Array}  ['item1', 'item1-1']
       */
      handleSelect (index, indexPath) {
        this.currentIndex = index
        this.$emit('select', index, indexPath)
      },
      /**
       * 
       * @event open
       * @param index {String} id
       * @param indexPath {Array}  ['item1', 'item1-1']
       */
      handleOpen (index, indexPath) {
        this.$emit('open', index, indexPath)
      },
      /**
       * 
       * @event close
       * @param index {String} id
       * @param indexPath {Array}  ['item1', 'item1-1']
       */
      handleClose (index, indexPath) {
        this.$emit('close', index, indexPath)
      }
    }
  }
</script>
