<template>
  <div ref="container"
       class="xdh-grid__container"
       :class="classes">
    <div class="xdh-grid__item"
         v-for="(item,index) in grids"
         :data-id="index"
         :key="index"
         :style="styles(item)">
      <slot :index="index" :item="item" :props="props">{{index}}</slot>
    </div>
  </div>

</template>

<script>
  /**
   * grid
   * @module widgets/xdh-grid
   */
  import Sortable from '../xdh-tabs/sortable'

  export default {
    name: 'XdhGrid',
    /**
     * 
     * @property {Boolean | Object} [sortable = false] sortable sort https://segmentfault.com/a/1190000008209715
     * @property {Array} [data] data list  value x
     * @property {Object} [props = {...}] props gridflex
     * @property {String} [props.order] order flexitem 0
     * @property {String} [props.basis] basis flexitem
     * @property {String} [props.grow] grow flexitem
     * @property {String} [props.shrink] shrink flex item
     * @property {String} [props.align] align flexitemitem align-items 
     * @property {String} [direction] direction flex, 'row', 'row-reverse', 'column', 'column-reverse'
     * @property {String} [wrap] wrap flex, 'nowrap', 'wrap', 'wrap-reverse'
     * @property {String} [justify] justify flex, 'flex-start', 'flex-end', 'center', 'space-between', 'space-around'
     * @property {String} [alignItems] alignItems flex, 'flex-start', 'flex-end', 'center', 'baseline', 'stretch'
     * @property {String} [alignContent] direction flex, 'flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'stretch'
     */
    props: {
      // sortablejs 
      sortable: {
        type: [Boolean, Object],
        default: false
      },
      // 
      data: {
        type: Array
      },
      // 
      props: {
        type: Object,
        default() {
          return {
            // item 0
            order: 'order',

            // item
            basis: 'basis',

            // item, 
            grow: 'grow',

            // item
            shrink: 'shrink',

            //  itemitem align-items 
            align: 'align'
          }
        }
      },
      // item
      direction: {
        type: String,
        validator(value) {
          return ['row', 'row-reverse', 'column', 'column-reverse'].includes(value)
        }
      },
      // item
      wrap: {
        type: String,
        validator(value) {
          return ['nowrap', 'wrap', 'wrap-reverse'].includes(value)
        }
      },
      // item
      justify: {
        type: String,
        validator(value) {
          return ['flex-start', 'flex-end', 'center', 'space-between', 'space-around'].includes(value)
        }
      },
      // item
      alignItems: {
        type: String,
        validator(value) {
          return ['flex-start', 'flex-end', 'center', 'baseline', 'stretch'].includes(value)
        }
      },
      // items
      alignContent: {
        type: String,
        validator(value) {
          return ['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'stretch'].includes(value)
        }
      }
    },
    data() {
      return {
        container: null,
        sortInstance: null,
        grids: []
      }
    },
    watch: {
      data: {
        deep: true,
        immediate: true,
        handler(val) {
          this.grids = val || []
        }
      },
      sortable(val) {
        if (this.sortInstance) {
          const options = val ? Object.assign({
            sort: true
          }, val) : {}
          this.setSortable(options)
        }
      }
    },
    computed: {
      // props  flex  
      classes() {
        return {
          [`xdh-grid--direction-${this.direction}`]: !!this.direction,
          [`xdh-grid--wrap-${this.wrap}`]: !!this.wrap,
          [`xdh-grid--justify-${this.justify}`]: !!this.justify,
          [`xdh-grid--align-items-${this.alignItems}`]: !!this.alignItems,
          [`xdh-grid--align-content-${this.alignContent}`]: !!this.alignContent,
          'xdh-grid--sortable': !!this.sortOptions
        }
      }
    },
    methods: {
      //  props.prop(obj)gridflex
      styles(item) {
        /**
         * gridflex, list"props"flex
         * @param {object} item
         */
        let style = {}
        if (item[this.props.order]) {
          style.order = item[this.props.order]
        }
        if (item[this.props.basis]) {
          style['flex-basis'] = item[this.props.basis]
        }
        if (item[this.props.grow]) {
          style['flex-grow'] = item[this.props.grow]
        }
        if (item[this.props.shrink]) {
          style['flex-shrink'] = item[this.props.shrink]
        }
        if (item[this.props.align]) {
          style['align-self'] = item[this.props.align]
        }
        return style
      },
      _initSortable() {
        if (!this.sortable) return

        const options = this.sortable ? Object.assign({
          sort: true,
          fallbackClass: 'xdh-tabs__clone'
        }, this.sortable) : {}
        this.sortInstance = new Sortable({
          vm: this,
          container: this.container,
          data: this.grids,
          config: {
            animation: 150,
            // el-tabstab id
            dataIdAttr: 'data-id',
            filter: '.is-disabled'
          },
          options: options,
          reset: function (n, index) {
            return index.toString()
          },
          change: this._handleChange
        })
      },
      _handleChange(data, type, args) {
        this.grids = data
        this.$emit('update:data', data)
        /**
         * 
         * @event on-change
         * @param {Array} data 
         * @param {string} type  sort / add /remove
         * @param {Array} args type  sort:[oldIndex, newIndex],add:[fromData, element, oldIndex, newIndex], remove:[element, oldIndex]
         */
        this.$emit('on-change', data, type, args)
      },
      setSortable(options) {
        this.sortInstance && this.sortInstance.setOption(options)
      }
    },
    mounted() {
      this.container = this.$refs.container
      this._initSortable()
    },
    beforeDestroy() {
      this.sortInstance && this.sortInstance.destroy()
    }
  }
</script>
