<template>
  <div class="xdh-nav-tabs" v-if="tabs && tabs.list.length>0">
    <el-tabs class="xdh-nav-tabs__tabs"
             :class="{'has-tool':showTool}"
             type="card"
             v-model="selected"
             @tab-remove="handleTabRemove"
             @tab-click="handleTabClick">
      <el-tab-pane v-for="(item,index) in tabs.list" :key="item.name"
                   :closable="item.closable"
                   :name="item.name">
        <template slot="label">
          <slot name="label" :item="item" :index="index"><i v-if="item.icon" :class="item.icon"></i> {{item.label}}
          </slot>
        </template>
      </el-tab-pane>
    </el-tabs>
    <div class="xdh-nav-tabs__tool" v-if="showTool">
      <el-dropdown size="small" @command="handelToolCommand">
        <span class="el-dropdown-link">
          <i class="el-icon-setting"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <slot name="tool"></slot>
          <el-dropdown-item :divided="$slots.tool" command="close"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>

  /**
   * nav-tabs 
   * @module widgets/xdh-nav-tabs
   */

  /*
   * @description createstore'xdhNavTab'store.state
   *
   *
   * @property {Array} [list] 
   * @property {String} [list.label] 
   * @property {String} [list.name] 
   * @property {Boolean} [list.closable] 
   * @property {String} [selected] name
   *
   */

  /**
   * @example
   *
   * //  router.js  navs-tabs store
   * import store from '../store/index'
   * import createStoreModule from '@/widgets/xdh-nav-tabs/store' // nav-tabsstore
   * const DEMO_TABS = 'demoTabs'
   * if (!store.state[DEMO_TABS]) { // store
   *   store.registerModule(DEMO_TABS, createStoreModule())
   * }
   * // demoTabs.statetabs
   * router.afterEach((to) => { store.commit(`${DEMO_TABS}/push`, toObj })
   *
   * scopeSlots
   * @member scopeSlots
   * @property {string} label , item index 
   *
   */
  import createStoreModule from './store'

  /**
   * slots
   * @member slots
   * @property {string} tool el-dropdown-item 
   */

  export default {
    name: 'XdhNavTabs',
    /**
     * 
     * @property {String} [label] 
     * @property {String} [name]  path
     * @property {String} [icon] 
     * @property {Boolean} [closable = true] 
     * @property {String} [store = xdhNavTab] 
     * @property {Boolean} [router = true]  vue-router  name  path 
     */
    props: {
      label: {
        type: String
      },
      name: {
        type: String
      },
      icon: {
        type: String
      },
      closable: {
        type: Boolean,
        default: true
      },
      store: {
        type: String,
        default: 'xdhNavTab'
      },
      router: {
        type: Boolean,
        default: true
      },
      showTool: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      tabs() {
        const store = this.$store.state[this.store]
        if (store) {
          return store
        } else {
          return {
            list: [],
            selected: ''
          }
        }
      },
      selected: {
        get() {
          return this.$store.state[this.store].selected
        },
        set(selected) {
          this.$store.commit(`${this.store}/select`, selected)
        }
      }
    },
    methods: {
      handleTabRemove(name) {
        this.$store.commit(`${this.store}/remove`, {name: name})
        /**
         * tab
         * @event on-remove
         * @param {string} name tab 
         * @param {string} selected tab
         */
        this.$emit('on-remove', name, this.tabs.selected)
        if (this.router) {
          this.$router.push(this.tabs.selected)
        }
      },
      handleTabClick(tab) {
        /**
         * tab
         * @event on-click
         * @param {object} tab tab Vue
         */
        this.$emit('on-click', tab)
        if (this.router) {
          // el-tabs
          this.$router.push(tab.name)
        }
      },
      /**
       * tab
       * @function push
       * @param {Object} tab
       * @param {string} tab.label 
       * @param {string} tab.name path
       * @param {string} tab.icon  className
       * @param {boolean} tab.closable 
       *
       */
      push(tab) {
        this.$store.commit(`${this.store}/push`, {tab})
      },
      handelToolCommand(command) {
        switch (command) {
          case 'close':
            this.$store.commit(`${this.store}/close`)
            break;
        }
        /**
         * 
         * @event on-command
         * @param {string} command 
         *
         */
        this.$emit('on-command', command)
      }
    },
    created() {
      /**
       *  store  xdhNavTab  xdhNavTab
       */
      if (!this.$store.state[this.store]) {
        this.$store.registerModule(this.store, createStoreModule())
      }
      if (this.label && this.name) {
        this.$store.commit(`${this.store}/push`, {
          tab: {
            label: this.label,
            name: this.name,
            icon: this.icon,
            closable: this.closable
          }
        })
      }
    }
  }
</script>
