<script>

  /**
   * XdhHeader 
   * @module widgets/xdh-header
   */

  import HeaderLogo from './components/header-logo'
  import HeaderNav from './components/header-nav'
  import HeaderSearch from './components/header-search'
  import HeaderTitle from './components/header-title'
  import HeaderTool from './components/header-tool'
  import HeaderUser from './components/header-user'
  import HeaderSlot from './components/header-slot'

  /**
   * slots
   * @member slots
   * @property {String} logo logo
   * @property {String} title 
   * @property {String} search 
   * @property {String} nav 
   * @property {String} user 
   * @property {String} tool 
   */

  export default {
    name: 'XdhHeader',
    components: {
      HeaderLogo,
      HeaderNav,
      HeaderSearch,
      HeaderTitle,
      HeaderTool,
      HeaderUser,
      HeaderSlot
    },
    /**
     * props
     * @member props
     * @property {String} [size]  largedefaultsmall
     * @property {String} [layout]  logo, title, search, nav, -> ,user, tool
     * @property {String} title title
     * @property {String} logo logologo
     * @property {Array} [searchTypes] 
     * @property {Array} [searchType] 
     * @property {String} [searchKeyword] 
     * @property {String} [searchWidth] 
     * @property {String} [searchPlaceholder] 
     * @property {Array} [nav] 
     * @property {Object} [navProp] 
     * @property {Boolean } [navRouter]  vue-router  index  path 
     * @property {String} [navDefaultActive] 
     * @property {String} [userIcon] Icon
     * @property {String} [userText] 
     * @property {Array} tools 
     * @property {Object} [toolProp] 
     * @property {Boolean} [toolInline]  
     */
    props: {
      // 
      size: {
        type: String,
        default: 'default',
        validator(val) {
          return ['large', 'default', 'small'].includes(val)
        }
      },
      // 
      layout: {
        type: String,
        default: 'title'
      },
      // title
      title: {
        type: String
      },
      // logo 
      logo: {
        type: String
      },
      // 
      searchTypes: {
        type: Array
      },
      // 
      searchType: {
        type: [String, Number]
      },
      searchKeyword: {
        type: String
      },
      // 
      searchWidth: {
        type: String
      },
      // 
      searchPlaceholder: {
        type: String
      },
      // 
      nav: {
        type: Array
      },
      // 
      navProp: {
        type: Object
      },
      //  vue-router  index  path 
      navRouter: Boolean,
      // 
      navDefaultActive: String,
      // Icon
      userIcon: String,
      // 
      userText: String,

      // 
      tools: Array,
      // 
      toolProp: Object,
      // 
      toolInline: Boolean
    },
    methods: {
      renderLogo(h) {
        return h('header-logo', {
          props: {
            src: this.logo
          }
        }, this.$slots.logo)
      },
      renderTitle(h) {
        return h('header-title', this.$slots.title || this.title)
      },
      renderSearch(h) {
        return h('header-search', {
          props: {
            width: this.searchWidth,
            size: this.size,
            placeholder: this.searchPlaceholder,
            types: this.searchTypes,
            keyword: this.searchKeyword,
            type: this.searchType
          },
          on: {
            search: (keyword, type) => {
              /**
               * 
               * @event on-search
               * @param {string} keyword 
               * @param {string} type  
               */
              this.$emit('on-search', keyword, type)
            }
          }
        }, this.$slots.search)
      },
      renderNav(h) {
        return h('header-nav', {
          props: {
            data: this.nav,
            props: this.navProp,
            router: this.navRouter,
            defaultActive: this.navDefaultActive
          },
          on: {
            select: (index, indexPath) => {
              /**
               * 
               * @event on-nav-select
               * @param {string} index 
               * @param {string} indexPath  
               */
              this.$emit('on-nav-select', index, indexPath)
            }
          }
        }, this.$slots.nav)
      },
      renderUser(h) {
        return h('header-user', {
          props: {
            icon: this.userIcon,
            text: this.userText
          }
        }, this.$slots.user)
      },
      renderTool(h) {
        return h('header-tool', {
          props: {
            data: this.tools,
            props: this.toolProp,
            inline: this.toolInline
          },
          on: {
            click: (item) => {
              /**
               * 
               * @event on-tool-click
               * @param {Object} item 
               */
              this.$emit('on-tool-click', item)
            }
          }
        }, this.$slots.tool)
      }
    },
    render(h) {
      let template = <div class={['xdh-header', `is-${this.size}`]}></div>
      const layout = this.layout || ''
      if (!layout) return
      const TEMPLATE_MAP = {
        logo: this.renderLogo(h),
        title: this.renderTitle(h),
        search: this.renderSearch(h),
        nav: this.renderNav(h),
        user: this.renderUser(h),
        tool: this.renderTool(h),
        slot: <header-slot></header-slot>
      };
      const components = layout.split(',').map((item) => item.trim());
      const rightWrapper = <div class="xdh-header__rightwrapper"></div>;
      let haveRightWrapper = false;
      template.children = template.children || [];
      rightWrapper.children = rightWrapper.children || [];
      components.forEach(compo => {
        if (compo === '->') {
          haveRightWrapper = true;
          return;
        }

        if (!haveRightWrapper) {
          template.children.push(TEMPLATE_MAP[compo]);
        } else {
          rightWrapper.children.push(TEMPLATE_MAP[compo]);
        }
      });

      if (haveRightWrapper) {
        template.children.unshift(rightWrapper);
      }

      return template;
    },
    created() {
    }
  }
</script>
